<template>
  <v-dialog
      v-model="show"
      @close="value = false"
      width="100%"
      max-width="800"
  >
    <v-card>
      <v-card-title>
        Пожертвование
      </v-card-title>

      <v-card-text>
        <p>Укажите сумму пожертвования</p>

        <v-row>
          <v-col cols="12" sm="4">
            <v-card hover :dark="form.selected === 100" :color="form.selected === 100 ? 'green' : ''"
                    @click="form.selected = 100" :disabled="requestInProgress">
              <v-card-title class="text-h3 justify-center">100&#8381;</v-card-title>
            </v-card>
          </v-col>

          <v-col cols="12" sm="4">
            <v-card hover :dark="form.selected === 500" :color="form.selected === 500 ? 'green' : ''"
                    @click="form.selected = 500" :disabled="requestInProgress">
              <v-card-title class="text-h3 justify-center">500&#8381;</v-card-title>
            </v-card>
          </v-col>

          <v-col cols="12" sm="4">
            <v-card hover :dark="form.selected === 1000" :color="form.selected === 1000 ? 'green' : ''"
                    @click="form.selected = 1000" :disabled="requestInProgress">
              <v-card-title class="text-h3 justify-center">1&nbsp;000&#8381;</v-card-title>
            </v-card>
          </v-col>
        </v-row>

        <v-form class="my-5" @submit.stop.prevent="doSubmit">
          <v-text-field
              v-model="form.email"
              :required="true"
              type="email"
              label="Адрес электронной почты"
              placeholder="username@example.com"
              hint="Email не передается третьим лицам и нигде не публикуется. Когда нужная сумма будет набрана и передана в фонд WWF, на этот адрес придет уведомление."
              persistent-hint
              outlined
              :disabled="requestInProgress"
          />

          <v-checkbox label="Хочу быть в списке участников" v-model="form.publish" :disabled="requestInProgress"/>

          <v-text-field
              v-if="form.publish"
              v-model="form.name"
              :required="form.publish"
              label="Ваше имя"
              placeholder="Иван Иванов"
              hint="Это имя будет указано в списке участников. Нецензурные имена будут скрыты звездочками."
              persistent-hint
              outlined
              :disabled="requestInProgress"
          />

          <div class="d-flex justify-center mt-5">
            <v-btn type="submit" dark large color="green" @click.stop.prevent="doSubmit" :disabled="requestInProgress">Пожертвовать</v-btn>
          </div>
          <p v-if="error" class="red--text text-center mt-3">{{ error }}</p>
        </v-form>

        <div ref="hidden-form" style="display: none;"></div>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import {getPaymentForm} from "../api";
import EmailValidator from "email-validator"

export default {
  name: "DonateModal",
  props: ["value"],
  data() {
    return {
      requestInProgress: false,
      form: {
        email: '',
        name: '',
        selected: 500,
        publish: false,
      },
      error: null,
    }
  },
  computed: {
    show: {
      get() {
        return this.value;
      },
      set(value) {
        if (this.requestInProgress) {
          console.debug('Cannot close dialog while request in progress');
          return;
        }

        this.$emit('input', value)
      }
    }
  },
  methods: {
    doSubmit() {
      if (!this.validate()) {
        console.debug("Form is invalid");
        return;
      }

      if (this.requestInProgress) {
        console.debug('Request already in progress');
        return;
      }
      this.requestInProgress = true;

      getPaymentForm(this.form.publish ? this.form.name : '$anonymous', this.form.email, this.form.selected)
        .then(r => {
          this.$refs['hidden-form'].innerHTML = r;
          this.$refs['hidden-form'].querySelector('button').click();
        })
        .catch(() => {
          this.error = "Ошибка отправки запроса";
        })
        .finally(() => {
          this.requestInProgress = false;
        })
    },
    validate() {
      if (!this.form.email.length) {
        this.error = "Укажите e-mail";
        return false;
      }

      if (!EmailValidator.validate(this.form.email)) {
        this.error = "Вы указали некорректный e-mail";
        return false;
      }

      if ([100, 500, 1000].indexOf(this.form.selected) === -1) {
        this.error = "Выберите сумму из предложенных вариантов";
        return false;
      }

      if (this.form.publish && !this.form.name.length) {
        this.error = "Необходимо указать имя";
        return false;
      }

      if (this.form.publish && (this.form.name.length < 2 || this.form.name.length > 100)) {
        this.error = "Имя должно содержать от 2 до 100 символов";
        return false;
      }

      this.error = null;
      return true;
    }
  }
}
</script>

