const BACKEND_HOST = process.env.VUE_APP_BACKEND_HOST || 'http://localhost:5000';

export function fetchProgress() {
    return fetch(`${BACKEND_HOST}/progress`)
        .then(r => r.json());
}

export function getPaymentForm(name, email, amount) {
    return fetch(`${BACKEND_HOST}/payment?name=${name}&email=${email}&amount=${amount}`)
        .then(r => r.text());
}