<template>
  <v-progress-linear
      height="50"
      :value="value"
      striped
      color="green"
  >
    <strong>Собрано {{ parseInt(amount).toLocaleString('ru') }}&#8381; из {{ parseInt(target).toLocaleString('ru') }}&#8381;</strong>
  </v-progress-linear>
</template>

<script>
export default {
  name: "Progress",
  props: ["amount", "target"],
  computed: {
    value() {
      return 100. * this.amount / this.target;
    }
  }
}
</script>

